@import '../../../../../commun/styles/sass/mixins';


.dialogsItemsContainer {
  @include flex(column, center, center, wrap);
  background-color: darkkhaki;
  margin: 10px 10px 10px 10px;



  .dialog a{
    @include flex(column, center, center, wrap);

    width: 100%;
    height: 100%;

    text-decoration: none;



  }
  .dialog a.active {
    color: gold;
  }
}